.one {
  width: 100vw;
  height: 130vh;
}

.main-logo-heading {
  position: absolute;
  top: 100px;
  left: 10%;
}

@-webkit-keyframes grow-top {
  0% {
    height: 0;
  }
  100% {
    height: 150px;
  }
}

@keyframes grow-top {
  0% {
    height: 0;
  }
  100% {
    height: 150px;
  }
}

.logo {
  background-image: url(/static/media/top-logo.3a4aa0ce.png);
  width: 180px;
  height: 150px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 95% 95%;
  -webkit-animation: grow-top 1s forwards;
          animation: grow-top 1s forwards;
}

.rainbow {
  background-image: url(/static/media/20127.2d0cdbcb.png);
  width: 90vw;
  height: 100vh;
  position: absolute;
  right: 0;
  top: 0;
  background-size: 100% 100%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-animation: grow-right 1s forwards;
          animation: grow-right 1s forwards;
}

@-webkit-keyframes grow-right {
  0% {
    width: 0;
  }
  100% {
    width: 90vw;
  }
}

@keyframes grow-right {
  0% {
    width: 0;
  }
  100% {
    width: 90vw;
  }
}

@media (max-width: 800px) {
  .rainbow {
    background-size: cover;
    width: 100vw !important;
  }
}

.social-links {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: absolute;
  top: 50vh;
  left: -160px;
  transform: rotate(270deg);
  z-index: 100;
  -webkit-animation: from-left 1s forwards;
          animation: from-left 1s forwards;
}

@-webkit-keyframes from-left {
  0% {
    left: -100vw;
  }
  100% {
    left: -160px;
  }
}

@keyframes from-left {
  0% {
    left: -100vw;
  }
  100% {
    left: -160px;
  }
}

.social-links p {
  margin-left: 50px;
  margin-top: 0;
  margin-bottom: 0;
}

@media (max-width: 600px) {
